<template>
  <v-container
    fluid
    fill-height
    style="padding:0px"
  >
    <v-layout
      justify-center
      align-center
    >
      <v-flex>
        <v-card
          color="white"
          width="100%"
        >
          <v-card-text>
            <v-form
              v-model="valid"
              @submit.prevent=""
              ref="form"
            >
              <v-container>
                <v-layout
                  row
                  wrap
                >
                  <v-flex
                    xs12
                    md6
                  >
                    <v-text-field
                      v-model="formFields.descricao"
                      label="Descrição"
                    ></v-text-field>
                  </v-flex>
                  <v-flex
                    xs12
                    md3
                  >
                    <v-text-field
                      v-model="formFields.cnpj"
                      mask="##.###.###/####-##"
                      label="CNPJ"
                      :rules="[formRules.default.required]"
                      required
                      return-masked-value
                    ></v-text-field>
                  </v-flex>                  
                  <v-flex
                    xs12
                    md3
                  >
                    <v-text-field
                      v-model="formFields.telefone"
                      label="Telefone"
                      return-masked-value
                      mask="(##) ##########"
                    ></v-text-field>
                  </v-flex>
                  <v-flex
                    xs6
                    md4
                  >
                    <v-layout>
                      <v-select
                        v-model="formFields.endereco_tipos_id"
                        :items="enderecoTiposOptions"
                        :loading="enderecoTiposOptionsLoad"
                        label="Tipo"
                        item-text="descricao"
                        item-value="id"                    
                      />
                      <endereco-tipo-light-form 
                        @success="enderecoTipoAddSuccess($event)"
                      />
                    </v-layout>
                  </v-flex> 
                  <v-flex
                    xs12
                    md2
                  >
                    <v-text-field
                      v-model="formFields.cep"
                      label="CEP"
                      return-masked-value
                      mask="##.###-###"
                      :rules="[formRules.default.required]"
                      required
                    ></v-text-field>
                  </v-flex>
                  <v-flex
                    xs12
                    md6
                  >
                    <v-text-field
                      v-model="formFields.logradouro"
                      label="Logradouro"
                      :rules="[formRules.default.required]"
                      required
                    ></v-text-field>
                  </v-flex>
                  <v-flex
                    xs12
                    md3
                  >
                    <v-select
                      v-model="estados_id"
                      :items="estadosOptions"
                      :loading="estadosOptionsLoad"
                      label="Estado"
                      item-text="nome"
                      item-value="id"                    
                    />
                  </v-flex>
                  <v-flex
                    xs12
                    md5
                  >
                    <v-select
                      v-model="formFields.cidades_id"
                      :items="cidadesOptions"
                      :loading="cidadesOptionsLoad"
                      label="Cidade"
                      item-text="nome"
                      item-value="id"   
                      no-data-text="Selecione um estado"
                      :rules="[formRules.default.required]"
                      required                 
                    />
                  </v-flex>
                  <v-flex
                    xs12
                    md4
                  >
                    <v-layout>
                      <v-select
                        v-model="formFields.bairros_id"
                        :items="bairrosOptions"
                        :loading="bairrosOptionsLoad"
                        label="Bairro"
                        item-text="nome"
                        item-value="id" 
                        no-data-text="Seleciona uma cidade"                   
                      />
                      <bairro-light-form 
                        :cidades-id="this.formFields.cidades_id"
                        @success="bairroAddSuccess($event)"
                      />
                    </v-layout>
                  </v-flex>
                  <v-flex
                    xs12
                    md5
                  >
                    <v-select
                      v-model="formFields.cliente_contatos_id"
                      :items="clienteContatosOptions"
                      :loading="clienteContatosOptionsLoad"
                      label="Contato"
                      item-text="nome"
                      item-value="id"                  
                    />
                  </v-flex> 
                  <v-flex
                    xs12
                    md12
                  >
                    <v-textarea
                      v-model="formFields.observacao"
                      label="Observações"
                    />
                  </v-flex>   
                </v-layout>
              </v-container>
            </v-form>
          </v-card-text>
          <v-divider />
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              flat
              large
              :loading="loading"
              @click="save"
            >
              Salvar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapMutations } from "vuex"
import BairroLightForm from "../components/shared/BairroLightForm/BairroLightForm"
import EnderecoTipoLightForm from "../components/shared/EnderecoTipoLightForm/EnderecoTipoLightForm"

import { ClienteEnderecosController } from "../controllers/ClienteEnderecosController"

import { EnderecoTiposController } from '../controllers/EnderecoTiposController';
import { EstadosController } from '../controllers/EstadosController';
import { CidadesController } from '../controllers/CidadesController';
import { ClienteContatosController } from '../controllers/ClienteContatosController';
import { ClientesController } from '../controllers/ClientesController';

export default {
  components: {
    BairroLightForm,
    EnderecoTipoLightForm
  },
  data(){
    return {
      loading: false,
      valid: false,
      formFields: {},
      formRules: {
        default: {
          required: value => !!value || "Campo obrigatório"
        }
      },
      
      clienteContatosOptions:[],
      clienteContatosOptionsLoad: false,

      enderecoTiposOptions:[],
      enderecoTiposOptionsLoad: false,

      estados_id: '',
      estadosOptions:[],
      estadosOptionsLoad: false,

      cidadesOptions:[],
      cidadesOptionsLoad: false,

      bairrosOptions:[],
      bairrosOptionsLoad: false,

      cliente: false
    }
  },

  methods: {
    ...mapMutations([
      "SHOW_ALERT",
      "SET_TOOLBAR_BACK_URL",
      "SHOW_LOADER",
      "CLOSE_LOADER"
    ]),

    async save() {
      if (this.valid) {
        this.loading = true

        let clienteEnderecosController = new ClienteEnderecosController()
        let result = await clienteEnderecosController.create(this.formFields)

        this.SHOW_ALERT({
          type: result.error ? "error" : "success",
          message: result.message
        })

        if (!result.error)
          this.$router.push({
            path: `/clientes/editar/${this.getClienteID()}/enderecos`
          })

        this.loading = false
      } else {
        this.$refs.form.validate()
      }
    },

    async loadCliente() {
      let clientesController = new ClientesController()
      let result = await clientesController.get(this.getClienteID())

      this.cliente = result.error ? false : result.data
    },

    async loadEnderecoTipos() {
      this.enderecoTiposOptionsLoad = true

      let enderecoTiposController = new EnderecoTiposController()
      let result = await enderecoTiposController.all()

      this.enderecoTiposOptions = result.data.data

      this.enderecoTiposOptionsLoad = false
    },

    async loadEstados() {
      this.enderecoTiposOptionsLoad = true

      let estadoController = new EstadosController()
      let result = await estadoController.all()

      this.estadosOptions = result.data.data

      this.enderecoTiposOptionsLoad = false
    },

    async loadCidades(estados_id) {
      this.cidadesOptionsLoad = true

      let cidadeController = new EstadosController()
      let result = await cidadeController.getCidades(estados_id)

      this.cidadesOptions = result.data

      this.formFields.cidades_id = null

      this.cidadesOptionsLoad = false
    },

    async loadBairros(cidades_id) {
      if(cidades_id){

          this.bairrosOptionsLoad = true

          let cidadeController = new CidadesController()
          let result = await cidadeController.getBairros(cidades_id)

          this.bairrosOptions = result.data


          this.bairrosOptionsLoad = false
      } else {
        this.bairrosOptions = []
      }
      this.formFields.bairros_id = null
    },

    async bairroAddSuccess(bairro) {
      await this.loadBairros(this.formFields.cidades_id)
      this.formFields.bairros_id = bairro.id
    },

    async enderecoTipoAddSuccess(enderecoTipo) {
      await this.loadEnderecoTipos()
      this.formFields.endereco_tipos_id = enderecoTipo.id
    },

    async loadContatos() {
      this.clienteContatosOptionsLoad = true

      let clienteContatoController = new ClienteContatosController()
      let result = await clienteContatoController.all(this.getClienteID())

      this.clienteContatosOptions = result.data.data

      this.formFields.bairros_id = null

      this.clienteContatosOptionsLoad = false
    },

    getClienteID() {
      return this.$route.params.id
    }
  },

  watch: {
    'estados_id': function () {
      this.loadCidades(this.estados_id)
    },
    'formFields.cidades_id': {
      handler () {
        this.loadBairros(this.formFields.cidades_id)
      },
      deep:true
    }
  },

  async created() {
    this.SHOW_LOADER()

    this.SET_TOOLBAR_BACK_URL(`/clientes/editar/${this.getClienteID()}/enderecos`)
    this.formFields = new ClienteEnderecosController().getModel()
    this.formFields.clientes_id = this.getClienteID()
    this.loadEnderecoTipos()
    this.loadEstados()
    this.loadContatos()
    await this.loadCliente()
    
    this.formFields.cnpj = this.cliente.cnpj
    this.formFields.telefone = this.cliente.telefone_principal

    this.CLOSE_LOADER()
  }
}
</script>